import uniCountDown from '@/components/countDown.vue';
export default {
	components: {
		uniCountDown
	},
	data() {
		return {
			color: '#666',
			backgroundColor: 'none',
			id: '',
			details: {},
			goods: {},
			obj: {},
			hour: 0,
			minute: 0,
			second: 0,
			show_time: false
		}
	},
	onLoad(options) {
		this.id = options.id
		this.obj = JSON.parse(options.obj)
		console.log(this.obj)
	},
	onShow() {
		this.getDetail()
	},
	methods: {
		getDetail() {
			let self = this
			let params = {
				id: self.id
			}
			self.$acFrame.HttpService.orderDetailSpeci(params).then(res => {
				console.log(res);
				if (res.success) {
					let _data = res.data
					_data.detail.goodsImgPath = self.setImg(_data.detail.goodsImgPath)
					self.details = _data;
					self.init_time(res.data.endTime);
				}
			})
		},
		init_time(endTime) {
			this.hour = this.$acFrame.Util.countTime(endTime, 'hour')
			this.minute = this.$acFrame.Util.countTime(endTime, 'minute')
			this.second = this.$acFrame.Util.countTime(endTime, 'second')
			this.show_time = false;
			setTimeout(() => {
				this.show_time = true
			}, 300)
		},
		setImg(url) {
			return this.$acFrame.Util.setImgUrl(url);
		},
		formatTime(t) {
			return this.$acFrame.Util.formatTime(t, "dayhm");
		},
		topay(id){
			this.$acFrame.HttpService.spellPay({id:id}).then(res=>{
				if(res.success){
					uni.navigateTo({
						url:'/pages/myshop/payWay?order='+JSON.stringify(res.data)
					}) 
				}
			})
		},
		textPaste() {
			var self = this;
			wx.showToast({
				title: '复制成功',
			})
			wx.setClipboardData({
				data: self.details.code,
				success: function(res) {
					wx.getClipboardData({
						success: function(res) {
							console.log(res.data)
						}
					})
				}
			})
		},
		phoneCall(phone){
			uni.makePhoneCall({
				phoneNumber:phone,
				fail:(error)=>{
					console.error(error)
				}
			})
		},
		cancelOrder() {
			let self = this
			let params = {
				spellIdUser:self.details.spellIdUser,
				spellId:self.details.spellId
			}
			
			self.$acFrame.HttpService.cancelSpellOrder(params).then(res => {
				if (res.success) {
					self.$acFrame.Util.mytotal('取消成功')
					setTimeout(()=>{
						uni.navigateBack({
							
						})
					},1000)
					
				}
			})
		}
	}
}
